import React, { useEffect, useState } from 'react';
import SelectUser from '../SelectUser/SelectUser';
// import VideoCard from '../VideoCard/VideoCard';

import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

import './AppointmentsList.css';


const useStyles = makeStyles((theme) => ({
  root: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '100%',
    },
  },
}));



const VideosList = (props) => {
  const classes = useStyles();

  const [videos, setVideos] = useState([]);
  const [modifiedVideos, setModifiedVideos] = useState([]);
  const [users, setUsers] = useState([]);
  const [selectedUser, setSelectedUser] = useState(null);
  const [isChanged, setChanged] = useState(false);

  useEffect(() => {
    if (selectedUser !== null) {
      console.log("selected");
      let modifiedVideos = videos;

      for (let i = 0; i < modifiedVideos.length; i++) {
        for (let j = 0; j < selectedUser.appointments.length; j++) {

          if (selectedUser.appointments[j].video === modifiedVideos[i]._id) {
            modifiedVideos[i].review = selectedUser.appointments[j].review;
            modifiedVideos[i].selected = true;
          } else {
            modifiedVideos[i].review = "";
            modifiedVideos[i].selected = false;
          }

        }
      }
      setModifiedVideos(modifiedVideos);
      setChanged(!isChanged);
    }
  }, [selectedUser])

  useEffect(() => {
    console.log(modifiedVideos)
  }, [isChanged])

  useEffect(() => {
  }, [modifiedVideos])

  useEffect(async () => {
    const response = await fetch(`http://localhost:3000/videos/`, {
      method: 'GET',
      mode: 'cors',
      headers: {
        'Content-Type': 'application/json'
      },
    });

    let data = await response.json();

    setVideos(data);
  }, [])

  useEffect(async () => {
    const response = await fetch(`http://localhost:3000/users/`, {
      method: 'GET',
      mode: 'cors',
      headers: {
        'Content-Type': 'application/json'
      },
    });

    let data = await response.json();

    setUsers(data);
  }, [])


  function getId(url) {
    const regExp = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|&v=)([^#&?]*).*/;
    const match = url.match(regExp);

    return (match && match[2].length === 11)
      ? match[2]
      : null;
  }

  const updateUser = async () => {
    console.log(selectedUser);
    const response = await fetch(`http://localhost:3000/users/${selectedUser._id}`, {
      method: 'PUT',
      mode: 'cors',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(selectedUser)
    });
  }

  const renderVideos = () => {
      return modifiedVideos.map((item, index) => {
      if (item.category === props.selectedCategory) {
      console.log(item._id, item.review)
      const videoId = getId(item.url);


        return (
          <div className={item.selected ? "video-container selected" : "video-container"} key={item._id} >
            <div className="video_iframe">
              <iframe width="100%" height="100%" src={`//www.youtube.com/embed/${videoId}`} frameBorder="0" allowFullScreen ></iframe>
            </div>

            <div className="video_info">
              <div className="video_select-bg" onClick={(e) => { setVideoSelected(index) }}></div>

              <p className="video_label">{item.name}</p>
              <p className="video_label">{item._id}</p>

              <div className="video_review">
                <TextField
                  className="video_review"
                  label="Комментарий к задаче видео"
                  multiline
                  rows={4}
                  variant="outlined"
                  defaultValue={item.review}
                  onChange={(e) => { setReview(e.target.value, index) }}
                />
              </div>

              <div className="video_counts" >

                <div className="count_item" >
                  <TextField
                    label="Повторений кол-во раз"
                    type="number"
                    InputProps={{ inputProps: { min: 1 } }}
                    InputLabelProps={{
                      shrink: true,
                    }}
                    variant="outlined"
                    onChange={(e) => { setCountRepeat(e.target.value, index) }}
                  />
                </div>

                <div className="count_item" >
                  <TextField
                    label="Повторений кол-во дней"
                    type="number"
                    InputProps={{ inputProps: { min: 1 } }}
                    InputLabelProps={{
                      shrink: true,
                    }}
                    variant="outlined"
                    onChange={(e) => { setCountDays(e.target.value, index) }}
                  />
                </div>

              </div>
            </div>
          </div>
        )
      }
    })
  }

  const setVideoSelected = async (index) => {
    // let video = {};
    // video.video = videos[index]._id;
    // video.review = videos[index].review || "";

    if (selectedUser !== null) {
      console.log("setVideoSelected", selectedUser);
      let newSelectedUser = selectedUser;

      if (newSelectedUser.appointments !== undefined) {

        let selectedVideoIndex = newSelectedUser.appointments.indexOf(videos[index]._id);

        if (selectedVideoIndex > -1) {
          newSelectedUser.appointments.splice(selectedVideoIndex, 1);
        } else {
          newSelectedUser.appointments.push(videos[index]._id)
        }

      } else {
        newSelectedUser.appointments = [];
        newSelectedUser.appointments.push(videos[index]._id);
      }
      await setChanged(!isChanged);
      await setSelectedUser(newSelectedUser);

    } else {
    }
  }

  const setReview = (value, index) => {
    let newVideos = [...videos];
    newVideos[index].review = value;
    setVideos(newVideos);
  };

  const setCountRepeat = (value, index) => {
    let newVideos = [...videos];
    newVideos[index].repeat = value;
    setVideos(newVideos);
  };

  const setCountDays = (value, index) => {
    let newVideos = [...videos];
    newVideos[index].days = value;
    setVideos(newVideos);
  };

  return (
    <div className="appointments">
      <SelectUser
        selectedUser={selectedUser}
        setSelectedUser={setSelectedUser}
        users={users}
      />
      <div className="video-list">
        {
          selectedUser !== null ?
            props.selectedCategory !== null ?
              renderVideos()
              :
              "Выберите категорию"
            :
            "Выберите пользователя"
        }
      </div>
      <Button
        variant="contained"
        color="primary"
        onClick={updateUser}>
        Сохранить
      </Button>
    </div>
  );
};

export default VideosList;